.App-social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.App-item {
  margin: 0 8px;
  width: 24px;
  height: 24px;
}

.app-icon {
  width: 24px;
  height: 24px;
}

.App-icon--contrast path{
  fill: var(--CONTRAST-COLOR);
}

.App-icon--principal path {
  fill: var(--PRINCIPAL-COLOR);
}

.App-icon:hover path {
  fill: var(--ACCENT-COLOR);
}